@import url("https://fonts.googleapis.com/css?family=Bungee+Shade|Source+Sans+Pro");



body {
  font-family: "Source Sans Pro", serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
  background-color: #000;
}

body:before {
  content: "";
  display: block;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: -10;
  background: url("images/runaway-back.jpg");
  background: no-repeat center center;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}

.carousel-container img {
  max-width: 100vw;
  width: 70vw;
  height: 60vh;
  text-align: center;
  background: transparent;
  display: flex;
  align-items: center;
  justify-content: center;
}
.carousel-container {
  width: 70vw;
  display: table;
  margin: 0 auto;
}
.bungee {
  font-family: "Bungee Shade", cursive;
}

h1 {
  font-family: "Bungee Shade", cursive;
}

h1,
h2,
h3,
h4,
h5,
p {
  color: white;
}

.main > .container {
  padding-top: 5rem;
}

@keyframes bounce {
  from {
    transform: translateY(0px);
  }
  to {
    transform: translateY(-20px);
  }
}

.bounce {
  animation: bounce 1.2s ease 0s infinite alternate;
}

.white {
  color: #fff;
}

#projects .showGrid p {
  margin-left: 15px;
}


